"use client"

import { useState, useEffect } from "react"
import { Plus, Search, Edit2, Trash2, X, Ruler, LayoutGrid, List } from "lucide-react"
import { mockSizes } from "@/lib/mock-data"

type Size = (typeof mockSizes)[0]
type ViewMode = "list" | "grid"

const VIEW_MODE_KEY = "adminTamanhosViewMode"

export default function AdminTamanhos() {
  const [sizes, setSizes] = useState(mockSizes)
  const [showModal, setShowModal] = useState(false)
  const [editingSize, setEditingSize] = useState<Size | null>(null)
  const [formData, setFormData] = useState({
    nome: "",
    ativo: true,
  })

  const [searchTerm, setSearchTerm] = useState("")
  
  const [viewMode, setViewMode] = useState<ViewMode>(() => {
    if (typeof window === "undefined") return "list"
    const saved = window.localStorage.getItem(VIEW_MODE_KEY) as ViewMode | null
    return saved === "list" || saved === "grid" ? saved : "list"
  })

  const changeViewMode = (mode: ViewMode) => {
    setViewMode(mode)
    if (typeof window !== "undefined") {
      window.localStorage.setItem(VIEW_MODE_KEY, mode)
    }
  }
  const filteredSizes = sizes.filter((s) =>
    s.nome.toLowerCase().includes(searchTerm.toLowerCase()),
  )

  const openCreateModal = () => {
    setEditingSize(null)
    setFormData({ nome: "", ativo: true })
    setShowModal(true)
  }

  const openEditModal = (size: Size) => {
    setEditingSize(size)
    setFormData({
      nome: size.nome,
      ativo: size.ativo,
    })
    setShowModal(true)
  }

  const handleSave = () => {
    if (editingSize) {
      setSizes(
        sizes.map((s) =>
          s.id === editingSize.id
            ? { ...s, nome: formData.nome, ativo: formData.ativo }
            : s,
        ),
      )
    } else {
      const newSize: Size = {
        id: (sizes.length + 1).toString(),
        nome: formData.nome,
        ativo: formData.ativo,
      }
      setSizes([...sizes, newSize])
    }
    setShowModal(false)
  }

  const handleDelete = (id: string) => {
    if (confirm("Tem certeza que deseja excluir este tamanho?")) {
      setSizes(sizes.filter((s) => s.id !== id))
    }
  }

  const toggleStatus = (id: string) => {
    setSizes(sizes.map((s) => (s.id === id ? { ...s, ativo: !s.ativo } : s)))
  }

  return (
    <div className="p-8">
      {/* Header */}
      <div className="flex items-center justify-between mb-8">
        <div>
          <h1 className="text-3xl font-light text-foreground">Tamanhos</h1>
          <p className="text-foreground/60">Gerencie os tamanhos dos produtos</p>
        </div>
        <button
          onClick={openCreateModal}
          className="flex items-center gap-2 bg-primary text-primary-foreground px-4 py-2 rounded-lg font-medium hover:bg-primary/90 transition-colors"
        >
          <Plus size={18} />
          Novo Tamanho
        </button>
      </div>

      {/* Filters & View Toggle – igual Cores */}
      <div className="flex flex-col sm:flex-row gap-4 mb-6 justify-between">
        <div className="relative flex-1 max-w-md">
          <Search
            size={18}
            className="absolute left-3 top-1/2 -translate-y-1/2 text-foreground/50"
          />
          <input
            type="text"
            placeholder="Buscar por nome..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="w-full pl-10 pr-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
          />
        </div>
        <div className="flex items-center bg-muted rounded-lg p-1 border border-border">
          <button
            onClick={() => changeViewMode("list")}
            className={`p-2 rounded-md transition-all ${
              viewMode === "list"
                ? "bg-background shadow-sm text-foreground"
                : "text-foreground/60 hover:text-foreground"
            }`}
          >
            <List size={18} />
          </button>
          <button
            onClick={() => changeViewMode("grid")}
            className={`p-2 rounded-md transition-all ${
              viewMode === "grid"
                ? "bg-background shadow-sm text-foreground"
                : "text-foreground/60 hover:text-foreground"
            }`}
          >
            <LayoutGrid size={18} />
          </button>
        </div>
      </div>

      {/* Content */}
      {viewMode === "grid" ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          {filteredSizes.map((size) => (
            <div
              key={size.id}
              className={`bg-card border rounded-xl p-6 transition-all ${
                size.ativo ? "border-border" : "border-border/50 opacity-60"
              }`}
            >
              <div className="flex items-start justify-between mb-4">
                <div className="p-3 rounded-lg bg-muted">
                  <Ruler size={24} className="text-accent" />
                </div>
                <div className="flex items-center gap-1">
                  <button
                    onClick={() => openEditModal(size)}
                    className="p-2 text-foreground/60 hover:text-primary hover:bg-muted rounded-lg transition-colors"
                  >
                    <Edit2 size={16} />
                  </button>
                  <button
                    onClick={() => handleDelete(size.id)}
                    className="p-2 text-foreground/60 hover:text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                  >
                    <Trash2 size={16} />
                  </button>
                </div>
              </div>

              <h3 className="font-medium text-foreground mb-4 text-xl">
                {size.nome}
              </h3>

              <div className="flex items-center justify-between">
                <span className="text-sm text-foreground/60">ID: {size.id}</span>
                <button
                  onClick={() => toggleStatus(size.id)}
                  className={`px-3 py-1 rounded-full text-xs font-medium transition-colors ${
                    size.ativo
                      ? "bg-green-100 text-green-700 hover:bg-green-200"
                      : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                  }`}
                >
                  {size.ativo ? "Ativo" : "Inativo"}
                </button>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div className="bg-card border border-border rounded-xl overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-muted border-b border-border">
                <tr>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    ID
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Nome
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Status
                  </th>
                  <th className="px-6 py-4 text-center text-sm font-medium text-foreground">
                    Ações
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-border">
                {filteredSizes.map((size) => (
                  <tr key={size.id} className="hover:bg-muted/50 transition-colors">
                    <td className="px-6 py-4 text-sm text-foreground/70">
                      {size.id}
                    </td>
                    <td className="px-6 py-4">
                      <div className="flex items-center gap-3">
                        <div className="p-2 rounded-lg bg-muted">
                          <Ruler size={16} className="text-accent" />
                        </div>
                        <span className="font-medium text-foreground">
                          {size.nome}
                        </span>
                      </div>
                    </td>
                    <td className="px-6 py-4">
                      <button
                        onClick={() => toggleStatus(size.id)}
                        className={`px-2 py-1 rounded-full text-xs font-medium transition-colors ${
                          size.ativo
                            ? "bg-green-100 text-green-700 hover:bg-green-200"
                            : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                        }`}
                      >
                        {size.ativo ? "Ativo" : "Inativo"}
                      </button>
                    </td>
                    <td className="px-6 py-4">
                      <div className="flex items-center justify-center gap-2">
                        <button
                          onClick={() => openEditModal(size)}
                          className="p-2 text-foreground/60 hover:text-primary hover:bg-muted rounded-lg transition-colors"
                        >
                          <Edit2 size={16} />
                        </button>
                        <button
                          onClick={() => handleDelete(size.id)}
                          className="p-2 text-foreground/60 hover:text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                        >
                          <Trash2 size={16} />
                        </button>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}

      {/* Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-card border border-border rounded-xl w-full max-w-md">
            <div className="flex items-center justify-between p-6 border-b border-border">
              <h2 className="text-xl font-medium text-foreground">
                {editingSize ? "Editar Tamanho" : "Novo Tamanho"}
              </h2>
              <button
                onClick={() => setShowModal(false)}
                className="text-foreground/60 hover:text-foreground"
              >
                <X size={20} />
              </button>
            </div>

            <div className="p-6 space-y-4">
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Nome do Tamanho
                </label>
                <input
                  type="text"
                  value={formData.nome}
                  onChange={(e) =>
                    setFormData({ ...formData, nome: e.target.value })
                  }
                  className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                  placeholder="Ex: P, M, G, 38, 40..."
                />
              </div>

              <div className="flex items-center gap-3">
                <input
                  type="checkbox"
                  id="ativo"
                  checked={formData.ativo}
                  onChange={(e) =>
                    setFormData({ ...formData, ativo: e.target.checked })
                  }
                  className="accent-accent"
                />
                <label htmlFor="ativo" className="text-sm text-foreground">
                  Tamanho ativo
                </label>
              </div>
            </div>

            <div className="flex items-center justify-end gap-3 p-6 border-t border-border">
              <button
                onClick={() => setShowModal(false)}
                className="px-4 py-2 border border-border rounded-lg hover:bg-muted transition-colors"
              >
                Cancelar
              </button>
              <button
                onClick={handleSave}
                className="px-4 py-2 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-colors"
              >
                {editingSize ? "Salvar" : "Criar"}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
